<div class="main-content" v-if="getFullWeather"><div class="main-content-left"><p class="main-content-left__degree">{{ Math.round(getFullWeather.current.temp) }}°</p><p class="main-content-left__today">Сегодня</p><p class="main-content-left__time">Время: 21:54</p><p class="main-content-left__city">Город: {{getFullWeather.town}}</p><div class="main-content-left__image"><img src="@/assets/img/sun.svg" alt="alt" v-if="description == 'ясно'"><img src="@/assets/img/rain.svg" alt="alt" v-else-if="description == 'дождь'"><img src="@/assets/img/mainly_cloudy.svg" alt="alt" v-else-if="description == 'пасмурно'"><img src="@/assets/img/small_rain_sun.svg" alt="alt" v-else-if="description == 'небольшой дождь и солнце'"><img src="@/assets/img/small_rain.svg" alt="alt" v-else-if="description == 'небольшой дождь'"></div></div><div class="main-content-right"><img src="@/assets/img/bg.svg" alt="alt"><div class="main-content-right-item"><img src="@/assets/img/icon-1.svg" alt="alt"><span>Температура</span><span>{{ Math.round(getFullWeather.current.temp) }}° - ощущается как {{ Math.round(getFullWeather.current.feels_like) }}° </span></div><div class="main-content-right-item"><img src="@/assets/img/icon-2.svg" alt="alt"><span>Давление </span><span>{{getFullWeather.current.pressure}} мм ртутного столба - нормальное</span></div><div class="main-content-right-item"><img src="@/assets/img/icon-3.svg" alt="alt"><span>Влажность</span><span>{{getFullWeather.current.humidity}}%</span></div><div class="main-content-right-item"><img src="@/assets/img/icon-4.svg" alt="alt"><span>Ветер</span><span>{{ getFullWeather.current.wind_speed }} м/с юго-запад - легкий ветер</span></div></div></div>