<div class="main-week-day"><h3>{{ getWeekDay}}</h3><h4>{{ getDay }} {{ getMonth }}</h4><img src="@/assets/img/sun.svg" alt="alt" v-if="main == 'Clear'"><img src="@/assets/img/mainly_cloudy.svg" alt="alt" v-if="main == 'Clouds'"><img src="@/assets/img/rain.svg" alt="alt" v-if="main == 'Rain'"><h5>{{ Math.round(day.temp.day) }}°</h5><h5>{{ Math.round(day.feels_like.day) }}°</h5><h6>+15° </h6><p>{{description}}</p></div>